@charset "UTF-8";
html,
body {
  margin: 0;
  padding: 0;
  height: 100%;
  color: #333;
  font-size: 16px;
}

h1,
h2,
h3,
h4,
h5,
h6,
.header {
  color: #b144ab;
  text-shadow: 0 0 1px #fff;
}
h1 svg,
h2 svg,
h3 svg,
h4 svg,
h5 svg,
h6 svg,
.header svg {
  fill: #b144ab;
  width: 18px;
  height: 18px;
  margin-right: 10px;
}

h1,
h2,
h3,
h4,
h5,
h6,
ul,
ol,
p {
  margin: 16px 0;
  padding: 0;
  list-style-type: none;
}

h1 {
  margin: 25px 0;
}
h1 svg {
  width: 24px;
  height: 24px;
}

abbr {
  cursor: help;
}

a {
  color: #0da1b8;
  text-decoration: none;
  transition: background-color, border-color, color, fill;
  transition-duration: 0.2s;
  transition-timing-function: ease-out;
}
a:hover {
  text-decoration: underline;
}

iframe {
  display: block;
  max-width: 100%;
  margin: 16px 0;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
  border: none;
}

h1 {
  font-size: 36px;
}

h2 {
  font-size: 28px;
}

h3 {
  font-size: 20px;
}

.wrapper {
  position: relative;
  box-sizing: border-box;
  min-height: 100%;
  padding-top: 70px;
  padding-bottom: 250px;
}

.masthead {
  box-sizing: border-box;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 70px;
  padding: 10px;
  border-bottom: 1px solid #ccc;
  background-color: rgba(255, 255, 255, 0.98);
  z-index: 1000;
  display: table;
}
@media all and (min-width: 768px) {
  .masthead {
    padding: 10px 15px;
  }
}
.masthead .main-header,
.masthead .main-nav {
  display: table-cell;
  vertical-align: middle;
}

.main-header a {
  color: #b144ab;
  line-height: 1em;
}
.main-header a:hover .logo {
  transform: scale(1.05, 1.05);
}
.main-header a:hover .logo-small {
  transform: scale(1.1, 1.1);
}
.main-header .logo,
.main-header .logo-small {
  transition: ease-in-out 0.1s all;
  vertical-align: middle;
  height: 40px;
}
.main-header .logo-small {
  display: inline-block;
}
.main-header .logo {
  display: none;
}
@media all and (min-width: 768px) {
  .main-header .logo-small {
    display: none;
  }
  .main-header .logo {
    display: inline-block;
  }
}

.main-nav {
  margin: 0 auto;
  text-align: right;
  font-size: 18px;
}
.main-nav ul {
  margin: 0;
}
.main-nav li {
  display: inline;
  margin-right: 20px;
}
.main-nav li:last-child {
  margin-right: 0;
}
.main-nav a {
  display: inline-block;
  font-family: "Varela Round", helvetica, arial, sans-serif;
  text-decoration: none;
}
.main-nav a.active {
  color: #b144ab;
}
.main-nav a.active span::after {
  content: "";
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  display: block;
  margin-top: 4px;
  border-bottom: 2px solid #b144ab;
}
.main-nav a .text {
  position: relative;
  display: none;
}
@media all and (min-width: 480px) {
  .main-nav a .text {
    display: inline;
  }
}
.main-nav a .text,
.main-nav a svg {
  vertical-align: middle;
}
.main-nav svg {
  fill: #0da1b8;
  width: 18px;
  height: 18px;
  margin-right: 10px;
  display: inline-block;
}
.main-nav a.active svg {
  fill: #b144ab;
}

.about-hero {
  width: 100%;
  margin: 0 auto;
}

.hero {
  position: relative;
  color: #fff;
  text-align: center;
  background-image: url("../img/hero.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}
.hero h1 {
  color: #fff;
  font-size: 28px;
}
.hero p {
  font-size: 20px;
}
@media all and (min-width: 768px) {
  .hero {
    text-align: left;
  }
  .hero h1 {
    font-size: 48px;
  }
  .hero p {
    font-size: 24px;
  }
}
.hero .overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(159, 61, 153, 0.7);
  z-index: 0;
}
.hero .hero-content {
  position: relative;
  z-index: 1;
  margin: 25% 0;
}

.social {
  display: block;
  list-style: none;
  margin: 12px 0;
  padding: 0;
}
.social::before, .social::after {
  content: "";
  display: table;
  clear: both;
}
.social li {
  display: inline-block;
  margin-right: 8px;
}
.social li:last-child {
  margin-right: 0;
}
.social svg {
  display: block;
  width: 32px;
  height: 32px;
  fill: #fff;
}

a svg {
  transition: ease-in-out 0.1s all;
}
a:hover svg {
  transform: scale(1.2, 1.2);
}

.meetup-link {
  color: #F64060;
}
.meetup-link svg {
  fill: #F64060;
  margin-right: 0;
}

h2 .meetup-link svg {
  width: 24px;
  height: 24px;
}

h3 .meetup-link svg {
  width: 18px;
  height: 18px;
  margin-bottom: -2px;
}

p .meetup-link svg {
  width: 18px;
  height: 18px;
  margin-left: 2px;
  margin-bottom: -3px;
}

.module {
  margin: 25px 0;
}
.module::before, .module::after {
  content: "";
  display: table;
  clear: both;
}
.module .column {
  margin-bottom: 25px;
}

.module-heading {
  font-size: 24px;
  padding-bottom: 10px;
  border-bottom: 2px solid #b144ab;
}

.footer {
  position: absolute;
  width: 100%;
  left: 0;
  bottom: 0;
  background-color: #421a40;
  color: #ccc;
}

.links li {
  display: inline;
}
.links li::after {
  content: " â€¢";
  margin: 0 8px;
  color: #ccc;
}
.links li:last-child::after {
  content: "";
}

.credits,
.credits a {
  font-size: 12px;
}

.photo-grid::before, .photo-grid::after {
  content: "";
  display: table;
  clear: both;
}
.photo-grid img {
  float: left;
  width: 50%;
}
@media (min-width: 768px) {
  .photo-grid img {
    width: 25%;
  }
}

.alert {
  color: #e35050;
}
.alert svg {
  fill: #e35050;
  width: 14px;
  height: 14px;
}
.alert a {
  color: #e35050;
  text-decoration: underline;
}

.wrapper {
  padding-bottom: 250px;
}

.no-wrap {
  white-space: nowrap;
}

.clear {
  clear: both;
}

.prose {
  font-size: 16px;
  line-height: 26px;
}
.prose h1,
.prose h2,
.prose h3,
.prose h4,
.prose h5,
.prose h6 {
  color: #333;
}

.prose ul {
  margin-left: 1.2em;
  list-style-type: square;
}

.prose ol {
  margin-left: 1.3em;
  list-style-type: decimal;
}

.prose .heading {
  margin-top: 24px;
  margin-bottom: 0;
}

.prose img {
  margin-top: 8px;
}

.prose .image {
  border: none;
}

.prose .bordered {
  padding: 4px;
  border: 1px solid #ccc;
}

.prose .bordered:hover {
  border-color: #0da1b8;
}

.bordered {
  display: inline-block;
  padding: 4px;
  border: 1px solid #ccc;
}

.bordered img {
  margin: 0;
}

.lanyrd-link {
  position: relative;
  top: -4px;
  left: 0px;
  display: inline-block;
  width: 32px;
  height: 32px;
  text-indent: -900em;
  background: url(../img/lanyrd-48x48.png) no-repeat center center;
  background-size: 16px 16px;
  z-index: 1;
  margin-bottom: -8px;
  vertical-align: text-top;
}

.container {
  max-width: 1024px;
  margin: 0 auto;
  padding: 0 16px;
}
.container::before, .container::after {
  content: "";
  display: table;
  width: 0;
  height: 0;
  clear: both;
}

.event-detail .lanyrd-link {
  top: 7px;
  left: 5px;
  width: 32px;
  height: 32px;
  background-size: 20px 20px;
}

.event-detail .meta {
  font-size: 18px;
}

.event-detail .description::before,
.event-detail .description::after {
  content: "";
  display: table;
  clear: both;
}

.event-detail .bordered {
  margin-bottom: 8px;
}

.event-detail .vevent {
  font-size: 16px;
  line-height: 20px;
  position: relative;
  padding: 14px 0 12px 58px;
  min-height: 40px;
  border-left: 0;
  border-right: 0;
  margin: 8px 0;
}

.event-detail .vevent .date {
  margin-top: -2px;
  display: block;
}

.event-detail .vevent .adr {
  display: none;
}

.event-detail .vevent .map {
  position: absolute;
  top: 8px;
  left: 0px;
  display: block;
}

.event-detail .vevent .map img {
  max-width: 40px;
  max-height: 40px;
}

.event-detail .vevent .globe {
  position: absolute;
  top: 20px;
  left: 0px;
  display: block;
}

.event-detail .sponsors {
  position: absolute;
  right: 0;
  top: 7px;
  position: static;
  overflow: hidden;
  margin-left: -58px;
  margin-top: 13px;
  padding-top: 8px;
}

.event-detail .sponsor-logo {
  float: right;
  float: none;
}

.event-detail .sponsor-logo img {
  display: block;
  max-height: 52px;
  max-width: auto;
}

.event-detail .description.image .bordered {
  float: left;
  width: 40%;
  max-width: 300px;
  margin: 4px 12px 12px 0;
}

.event-item {
  margin: 0 0 16px 0;
}
.event-item h3,
.event-item p {
  margin: 4px 0;
}

.event-item .lanyrd-simple {
  font-size: 13px;
  line-height: 1;
  color: #2e2e2e;
}

.news {
  margin-top: -20px;
}

.news:first-of-type {
  margin-top: 0;
}

.news:after {
  content: "";
  margin: 0;
}

.news a {
  display: block;
  padding: 8px 0;
  height: auto;
  line-height: 1;
}

.news:hover a {
  color: #333;
  border-color: #aaa;
}

.news strong {
  font-weight: normal;
}

.news-item .read-more {
  margin-top: -16px;
}

.dharmafly {
  text-decoration: none;
  padding: 1px;
  font-size: 11px;
}

.dharmafly:hover,
.dharmafly:focus {
  color: #a6c664;
}

.dharmafly:active {
  color: #fff;
  background-color: #a6c664;
  border-bottom: none;
}

.extra .heading {
  margin-bottom: 8px;
  margin-top: 0;
}

.lanyrd-people {
  list-style-type: none;
  overflow: hidden;
  position: relative;
  left: -8px;
  margin-bottom: 0;
}

.lanyrd-people {
  margin-bottom: 8px;
}

.lanyrd-people li {
  float: left;
  margin-left: 8px;
}

.lanyrd-people li a {
  width: 50px;
}

.lanyrd-trackers li a {
  width: 35px;
}

.hubbub {
  margin-top: 24px;
}

.hubbub-avatar {
  width: 40px;
}

.hubbub-avatar-link {
  float: left;
  margin-top: 4px;
}

.hubbub-content {
  margin-left: 60px;
}

.hubbub-timestamp {
  float: right;
}

.hubbub-header a {
  margin-right: 5px;
}

.hubbub-comment-body p {
  margin-top: 8px;
  margin-bottom: 16px;
}

.iframe-wrapper {
  position: relative;
  padding-bottom: 65%;
  margin-bottom: 20px;
}

.iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: none;
  max-width: 100%;
}

.iframe-slides .iframe {
  width: 200%;
  height: 200%;
  max-width: 200%;
  transform: scale(0.62);
  transform-origin: 0;
}

#media-test {
  display: none;
  visibility: hidden;
  width: 0;
}

@media (min-width: 480px) {
  h2 {
    font-size: 36px;
  }

  .event-detail .description.image {
    position: relative;
  }

  .event-detail .description.image .bordered {
    float: right;
    margin: 4px 0 12px 12px;
  }

  #media-test {
    width: 1px;
  }

  .event-detail .sponsors {
    position: absolute;
    right: 0;
    top: 7px;
    border-top: none;
    padding: 0;
    margin: 0;
  }

  .event-detail .sponsors .sponsor-logo {
    float: right;
  }
}
@media (min-width: 768px) {
  .column {
    float: left;
    box-sizing: border-box;
    width: 45%;
    margin-right: 5%;
  }

  .column:nth-child(odd) {
    clear: left;
  }

  .column:after {
    content: "";
  }
}
body,
.masthead p em {
  font-family: "Open Sans", helvetica, arial, sans-serif;
  font-weight: 400;
}

i,
em {
  font-family: "Open Sans", helvetica, arial, sans-serif;
}

b, strong,
.read-more a,
.event-detail .vevent,
.event-item .lanyrd-simple {
  font-family: "Open Sans", helvetica, arial, sans-serif;
  font-weight: 600;
}

h1,
h2,
h3,
h4,
h5,
h6,
.header,
.news strong {
  font-family: "Varela Round", helvetica, arial, sans-serif;
  font-weight: 400;
  text-rendering: optimizeLegibility;
}